import React, {Component} from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import { removeVoucher } from "eshop/modules/cart/actions/cart";
import VoucherSubType from "../VoucherSubType";
import {postLowerInstallments} from "../../../actions/cart";

const preventDefaultWrapper = (handler) => (event) => {
    event.preventDefault();
    handler(event);
}


export class ShowVoucherMobileComponent extends Component {

    constructor(props){
        super(props)
    }

    removeVoucher(event) {
        event.preventDefault();
        this.props.removeVoucher(this.props.entry.entryNo, this.props.entry.bundleNo, this.props.voucherSubType);
        if (this.props.lowerInstallmentsVisible) {
            this.props.postLowerInstallments(this.props.entry.bundleNo, 0, "")
        }
    }

    render() {
        return (
            <div className="opl-msg opl-msg--box opl-msg--info u-padding-all u-margin-l">
	            <p className="u-padding-left u-font-bold g-black1-c">{ this.props.voucherName }</p>
	            <br />
                { this.props.removable &&
                    <a href="#" onClick={this.removeVoucher.bind(this)} className="u-padding-left g-black1-c">Usu≈Ñ</a>
                }
            </div>
        )
    }
}

ShowVoucherMobileComponent.propTypes = {
    removable: PropTypes.bool,
    voucherName: PropTypes.string,
    voucherSubType: PropTypes.oneOf(Object.keys(VoucherSubType))
}

ShowVoucherMobileComponent.defaultProps = {
    removable: true,
    voucherName: ''
}

const mapDispatchToProps = (dispatch) => ({
    removeVoucher: (entryNo, bundleNo, voucherSubType) => dispatch(removeVoucher(entryNo, bundleNo, voucherSubType)),
    postLowerInstallments: (bundleNo, diff, market) => dispatch(postLowerInstallments(bundleNo, diff, market)),
});

export default connect(null, mapDispatchToProps)(ShowVoucherMobileComponent);